@import url('https://fonts.cdnfonts.com/css/mark-pro');

@font-face {
    font-family: MarkPro;
    src: url("assets/Mark-Pro.ttf");
}

body {
    text-align: center !important;
    font-size: 20px;
    padding: 2.5%;
    font-family: 'Mark Pro', Helvetica, sans-serif !important;
    font-weight: 500 !important;
    overflow-y: scroll;
    background-color: rgb(233, 233, 233) !important;
}

h1, h2, h3, h4, h5 {
    font-family: 'Mark Pro';
    font-weight: 800 !important;
}

.profilePic {
    width: 200px;
    height: 200px;
    border-radius: 100px;
}

.title-fade-out {
    opacity: 0;
    animation-name: fade-out;
    animation-duration: 2s;
}

.title-fade-in {
    opacity: 100;
    animation-name: fade-in;
    animation-duration: 2s;
}

@keyframes fade-out {
    from {opacity: 100}
    to {opacity: 0;}
}

@keyframes fade-in {
    from {opacity: 0;}
    to {opacity: 100;}
}